.datacard {
  width: 20%;
  height: 100%;
  margin: 5px 10px;
  text-align: center;
  box-shadow: 1px 1px 12px 1px gray;
}
#confirmed {
  background: linear-gradient(
    to bottom,
    rgb(241, 175, 175),
    rgb(248, 211, 211)
  );
}

#recovered {
  background: linear-gradient(to bottom, rgb(49, 190, 84), rgb(169, 241, 187));
}

#deceased {
  background: linear-gradient(to bottom, rgb(253, 93, 93), rgb(245, 182, 182));
}
