.stategrid {
  display: grid;
  grid-template-columns: 20vw 20vw 20vw 20vw;
  grid-template-rows: 6vh;
}

td {
  padding: 15px;
  width: 25%;
  background-color: white;
  border-radius: 5px;
  height: 50px;
  text-align: center;
}

tr:not(thead > tr) {
  width: 90vw;
  box-shadow: 1px 1px 12px rgb(187, 185, 185);
}

table {
  border-collapse: separate;
  border-spacing: 5px 10px;
  width: 90%;
}

.fa-arrow-up {
  color: red;
}

.fa-arrow-down {
  color: green;
}
